@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

html {
  font-family: "Rubik", sans-serif;
}

body {
  background-color: black;
}

.ip-data_data-container__3qJYH {
  background-image: url(/iptracker/static/media/pattern-bg.a60a3c85.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 30rem;
  text-align: center;
  padding: 3rem 2.5rem 0 2.5rem;
}

.ip-data_data-container__3qJYH h1 {
  color: #fff;
  font-size: 2.5rem;
  font-weight: 500;
  margin-bottom: 3.5rem;
}

.ip-data_data-container__3qJYH form {
  background-color: #fff;
  border-radius: 1rem;
  height: 5rem;
  display: flex;
  justify-content: space-between;
}

.ip-data_data-container__3qJYH input {
  font-family: "Rubik", sans-serif;
  font-size: 1.8rem;
  justify-self: flex-end;
  border: none;
  margin-left: 2rem;
}

.ip-data_data-container__3qJYH input:focus {
  outline: none;
}

.ip-data_data-container__3qJYH button {
  background-color: black;
  border: none;
  border-radius: inherit;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  padding: 1rem 2rem;
}

.ip-data_info-container__24yoC {
  background-color: #fff;
  border-radius: 1rem;
  margin-top: 2.5rem;
  padding-top: 2.5rem;
}

.ip-data_info-container__24yoC div {
  padding-bottom: 2.5rem;
}

.ip-data_info-container__24yoC h6 {
  color: hsl(0, 0%, 59%);
  font-weight: 500;
  letter-spacing: 0.2rem;
  margin-bottom: 1rem;
}

.ip-data_info-container__24yoC p {
  font-size: 2rem;
}

